  grid-row: 1;
  justify-content: center;
  padding: 4px;
}
.viewer-layout-sidebar {
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  display: none;
  -ms-grid-column: 1;
  grid-column: 1;
  -ms-grid-row: 2;
  grid-row: 2;
  justify-content: center;
}
